export default {
	props: {
		value: {
			type: [String, Number],
			default: '',
		},
		placeholder:{
			type:String,
			default: '',
		},
		type:{
			type:String,
			default: 'text',
		}
	},
	data() {
		return {
			className: '',
			InputValue: this.value,
			clearIconShow: false,
		}
	},
	methods: {
		showFocus() {
			this.className = `wa-input-focus`;
		},
		showBlur() {
			this.className = '';
		},
		clearValue(){
			this.InputValue = '';
			this.$emit('input','');
		},
	},
	watch: {
		InputValue: {
			handler(newValue, oldValue) {
				this.$emit('input',newValue);
				if (this.InputValue == '') {
					this.clearIconShow = false;
				} else {
					this.clearIconShow = true;
				}
			},
			immediate: true
		}
	},
	mounted(){

	}
}
